:root{
  --mainBlue:#000000;
  --lightBlue:#f0a500;
  --mainWhite:#f3f3f3;
  --mainDark:#232528;
  --mainYellow:#f0a500;
}
body{
 
  font-family: 'Noto Sans JP', sans-serif !important;
  background: var(--mainWhite) !important;
  color: var(--mainDark) !important;
}
.text-title{
  font-family: 'Bungee Shade', cursive;
  letter-spacing: .3rem;
  text-transform: uppercase;
}
.text-blue{
  color: var(--mainWhite);
}
.text-bright{
  color: var(--lightBlue);
}
.llt{
  font-size: .8rem !important;
}
.ttt{
  position: absolute;
  margin: 0 auto;
  padding-right: 5px;
}
.filter-green {
  filter: invert(58%) sepia(90%) saturate(1140%) hue-rotate(6deg) brightness(100%) contrast(103%);
}
.btn-black{
  background: transparent;
  text-transform: capitalize;
  font-size: .8rem !important;
  color:var(--mainDark);
  border-radius: 0 !important;
  border: .1rem solid var(--mainDark) !important;
}
.btn-black:hover{
  background: var(--mainDark)!important;
  color:var(--mainWhite)!important;
}
.cart-icon{
  cursor: pointer;
  font-size: 1.2rem;
  color: var(--mainYellow);

}